.howitmade{
    width: 100%;
    padding-top: 125px;
    margin: 0;
    background-color: $footer-bg-color;
    background-image: url('./../images/milk_mobile.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0px 766px;
    @media screen and (min-width: 768px){
        padding-top: 130px;
        background-image: none;
        }
    @media screen and (min-width: 1280px){
            padding-top: 125px;
            background-image: url(./../images/milk_decktop.png);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            background-position: 0px 342px;
        }
}

.title1_howitmade {
    font-family: Titan One;
    color: $pink-color;
    font-size: 18px;
    line-height: 21px;
    font-weight: normal;
    margin-bottom: 15px;
    text-transform: uppercase;
    text-align: center;
    @media screen and (min-width: 768px){
        margin-bottom: 16px;
        font-size: 26px;
        line-height: 30px;
        }
    @media screen and (min-width: 1280px){
        font-size: 30px;
        line-height: 34px;
        margin-bottom: 15px;    
        }
    }
.title2_howitmade  {
    font-family: Titan One;
    color: $red-color;
    font-weight: 400px;
    font-size: 34px;
    line-height: 38,93px;
    letter-spacing: 0.04em;
    margin-bottom: 26px;
    text-transform: uppercase;
    text-align: center;
    @media screen and (min-width: 768px){
        margin-bottom: 50px;
        font-size: 48px;
        line-height: 55px;
        }
    @media screen and (min-width: 1280px){
        font-size: 58px;
        line-height: 66px;
        margin-top: 15px;
        margin-bottom: 0px;
        }
}
.text_howitmade {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 30px;
    @media screen and (min-width: 768px){
        flex-direction: row;
        justify-content: space-between;
    }
}
