.button_readmore {
    font-family: DM Sans;
    width: 176px;
    height: 44px;
    display: flex;
    white-space: nowrap;
    align-items: center;
    text-align: center;
    border-radius: 22px;
    border: none;
    border-radius: 22px;
    font-size: 16px;
    line-height: 20,83px;
    padding-left: 37px;
    margin-top: 17px;

&:hover{
    background-color: $hover-buttom-howitmade;
    color: $red-color;
}
    @media screen and (min-width: 768px){
        margin-top: 62px;
    }
    @media screen and (min-width: 1280px){
